.header-section {
	position: relative;
	height: 100vh;
	width: 100%;
	.scroll-icon {
		position: relative !important;
		z-index: 10;
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		transform: translateY(30%);
		width: 180px;
		z-index: 5;
		img,
		div {
			transform-origin: 0% 0%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 180px;
		img {
			transform-origin: 100% 0%;
		}
	}
	.elements-absolute {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		.background-element {
			display: block;
			height: 100%;
			.background-wrapper {
				width: 100%;
				height: 100%;
				display: inline-block;
				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 6/4;
		margin: 0 auto;
		margin-top: 3rem;
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			padding: 8px;
			.background-header {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: var(--border-radius);
			}
		}
	}
	.header-content {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		.header {
			position: relative;
			display: block;
			width: 50%;
			padding: 4rem 0;
			@include max-lg {
				width: 80%;
			}
			@include max-md {
				width: 100%;
				padding: 4rem 0;
			}
			.header-title {
				position: relative;
				text-align: center;
				&-content {
					position: relative;
					padding: 3rem 0;
					border-radius: var(--border-radius);
					@include max-md {
						padding: 2rem 1rem;
					}
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}
