.couple-section {
	position: relative;
	padding: 5rem 0 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0 0;
	}
	.orn-1 {
		position: absolute;
		left: 0;
		top: 3rem;
		width: 100%;
		transform: translateX(-30%);
	}
	.orn-2 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100%;
		transform: translateX(20%);
	}
	.orn-3 {
		position: absolute;
		left: 0;
		top: 0;
		width: 160px;
		z-index: 5;
		img {
			transform-origin: 0% 0%;
		}
		@include max-lg {
			width: 120px;
		}
		@include max-md {
			width: 90px;
		}
	}
	.orn-4 {
		position: absolute;
		top: 0;
		right: 0;
		width: 320px;
		transform: translateX(80%) scale(-1);
		z-index: 5;
		@include max-md {
			width: 240px;
		}
	}
	.elements-absolute {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 100%;
		.background-element {
			display: block;
			height: 100%;
			.background-wrapper {
				width: 100%;
				height: 100%;
				display: inline-block;
				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
					border-radius: var(--border-radius);
				}
			}
		}
	}
	.and {
		@include min-md {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		font-weight: 600;
		color: var(--color-dark);
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		z-index: 3;
		span {
			position: relative;
			font-size: 24px;
			font-family: $font-secondary;
		}
	}
	.couple-content {
		position: relative;
		margin-bottom: 10rem;
	}
	.couple {
		position: relative;
		text-align: center;
		padding: 1rem 0;
		background-color: var(--color-primary);
		border-radius: var(--border-radius);
		height: 100%;
		z-index: 5;
		.image-wrap {
			width: 80%;
			position: relative;
			aspect-ratio: 9/10;
			border-radius: 1000px 1000px 50px 50px;
			margin: -10rem auto 0;
			&.man {
				.overlay-image {
					transform: translate(-3%, -3%);
				}
			}
			&.woman {
				.overlay-image {
					transform: translate(3%, -3%);
				}
				@include max-md {
					margin-top: 2rem;
				}
			}
			.couple-image {
				height: 100%;
				object-fit: cover;
				position: relative;
				width: 100%;
				border-radius: 1000px 1000px 50px 50px;
			}
		}
		.couple-description {
			position: relative;
			padding: 1rem;
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: 100%;
			.sosmed {
				color: var(--color-button-trans);
				text-decoration: none;
				padding: 0.5rem 1.5rem;
				border-radius: 50px;
				background: transparent;
				border: 1px solid var(--color-dark);
				transition: all 0.25s ease;
				display: block;
				width: fit-content;
				margin: 0 auto;
				&:hover {
					background-color: var(--color-secondary);
					border: 1px solid var(--color-secondary);
					color: var(--text-button);
				}
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
	.reminder-wrap {
		padding: 3rem 0 5rem;
		@include max-md {
			padding: 3rem 0;
		}
		.elements-wrapper-absolute {
			height: calc(100% + 5rem);
		}
	}
}
