.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	height: 100vh;
	width: 100%;
	.orn-1 {
		position: absolute;
		width: 140%;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%) scaleY(-1) rotateZ(-50deg);
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 4rem 0 3rem;
		@include max-lg {
			padding: 4rem 0 8rem;
			align-items: center;
		}
		.cover-content {
			text-align: center;
			position: relative;
			background-color: transparent;
			border-radius: 12px;
			color: var(--color-dark);
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			@include max-mhc(667px) {
				align-items: flex-start;
			}
			.cover-body {
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				&::before {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					background-color: var(--color-light);
					opacity: 0.7;
					content: "";
					border-radius: var(--border-radius);
				}
				@include max-md {
					display: grid;
					grid-template-rows: 1fr auto;
					height: 100%;
					@include min-mhc(796px) {
						grid-template-rows: auto auto;
					}
					.col-12 {
						height: 100%;
					}
				}
				.greeting-wrapper {
					position: relative;
					@include max-md {
						font-size: 14px;
					}
					h6 {
						font-weight: 700;
					}
				}
				h6 {
					font-weight: 600;
				}
				.image-wrapper {
					position: relative;
					width: 100%;
					height: auto;
					aspect-ratio: 1;
					border-radius: 12px;
					@include max-md {
						width: 100%;
						height: 100%;
						aspect-ratio: unset;
					}
					@include min-mhc(796px) {
						width: 100%;
						height: auto;
						aspect-ratio: 1;
					}
					.cover-background {
						width: 100%;
						height: 100%;
						object-fit: cover;
						border-radius: 12px;
						position: relative;
					}
				}
				.cover-description {
					padding: 0 1.5rem;
					text-align: center;
					@include max-md {
						padding: 2rem 0 1rem;
					}
					* {
						position: relative;
					}
				}
			}
			.elements-widget {
				width: 100%;
			}
		}
	}
}
