.story-section {
	position: relative;
	padding: 15rem 0 5rem;
	.without-image {
		padding: 5rem 0;
	}
	@include max-md {
		padding: 13rem 0 3rem;
		.without-image {
			padding: 3rem 0;
		}
	}
	.orn-1 {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: 160%;
	}
	.story-content {
		position: relative;
		border-radius: var(--border-radius);
		background-color: var(--color-primary);
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		align-items: center;
	}
	.story-description {
		position: relative;
		text-align: center;
		padding: 3rem 1rem;
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		aspect-ratio: 4/3;
		z-index: 3;
		margin-top: -10rem;
		padding: 8px;
		.story-background {
			position: relative;
			object-fit: cover;
			width: 100%;
			height: 100%;
			border-radius: var(--border-radius);
		}
	}
}
