.apology-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		bottom: 5rem;
		right: 0;
		transform: translateX(60%);
		width: 250px;
	}
	.appology-wrap {
		position: relative;
		padding: 3rem 2rem;
		background-color: var(--color-primary);
		border-radius: var(--border-radius);
		@include max-md {
			padding: 2rem 1rem;
		}
		.background-element {
			img {
				border-radius: var(--border-radius);
			}
		}
	}
}
