.thank-section {
	position: relative;
	padding: 10rem 0 5rem;
	text-align: center;
	@include max-md {
		padding: 5rem 0 3rem;
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 4/3;
		padding: 8px;
		.thank-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: var(--border-radius);
		}
	}
	.thank-wrapper {
		position: relative;
		border-radius: var(--border-radius);
		@include max-md {
			.row {
				gap: 2rem;
			}
		}
		.thank {
			position: relative;
			padding: 3rem 2rem;
			height: 100%;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			z-index: 5;
			@include max-md {
				padding: 2rem 1rem;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
