.rsvp-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.rsvp-wrapper {
		position: relative;
		padding: 3rem 0;
		@include max-lg {
			padding: 1rem 0;
		}
		.rsvp-form {
			padding: 2rem;
			position: relative;
			@include max-md {
				padding: 2rem 1rem;
			}
		}
	}
}
